import service from '../../service.js';
import mInput from '../../components/m-input.vue';
export default {
	components: {
		mInput
	},
	data() {
		return {
			account: '',
			vercode: '',
			password: '',
			confirmpassword: '',
			countdown: 60
		}
	},
	methods: {
		numberst(e) {
			//其他代码....
			this.countDown();
		},
		// 倒计时
		countDown() {
			let self = this;
			self.countdown = 60;
			self.countdown -= 1;
			if (self.clear) {
				clearInterval(self.clear)
			}
			self.clear = null;
			self.clear = setInterval(_ => {
				if (self.countdown > 0) {
					self.countdown -= 1;
				} else {
					clearInterval(self.clear)
				}
			}, 1000)
		},
		findPassword() {
			/**
			 * 仅做示例
			 */
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.vercode.length != 6) {
				uni.showToast({
					icon: 'none',
					title: '验证码不正确'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.confirmpassword != this.password) {
				uni.showToast({
					icon: 'none',
					title: '两次密码不一致'
				});
				return;
			}
			uni.showToast({
				icon: 'none',
				title: '重置密码成功',
				duration: 3000
			});
		}
	}
}
